/**
 * components
 * 自定义功能组件
 *
 */
export default {
	path: '/components',
  components: {
    comp:resolve => {require(['@/components'], resolve);}
  },
	children:[
		{
			path: '/components/pull',
			components: {
				compLists:resolve => {require(['@/view/components-demo/pull'], resolve);}
			}
		}
	]
}





